.button {
    padding: 0.7rem 1.2rem;
    margin: 0.5rem;
    background-color: rgb(255, 206, 151);
    color: rgb(0, 0, 0);
    border-radius: 50px;
    text-align: center;
}

.button:hover {
    background-color: rgb(0, 0, 0);
    color: rgb(245, 245, 245);
    transition: 0.5s ease-in-out;
}
